@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}
#root{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;    
    height: 100vh !important;
}
body{
    height: 100vh;
}
a:hover{
    color: white;
}
.row {
    margin-right: 0px;
    margin-left: 0px;
}
p{
    margin-bottom: 0px;
}




/* home page styling starts here */
#header{
    width: 100%;
    margin-top: 40px;
}
#header h1{
    margin: 0 0 10px 0;
    font-size: 40px;
    font-weight: 700;
    color: black;
}
#header .brand-name{
    color: #3498db;
}
#header h2{
    color: #484848;
    font-size: 24px;
}
#header .btn-get-started{
    font-size: 16px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: .5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
}
#header .btn-get-started:hover{
    color: white;
    background-color: #3498db;
}
.header-img{
    text-align: right;
}
#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down {
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}
@keyframes up-down {
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}
/* home page styling ends here */

/* responsive styling starts here */
@media(max-width:500px){
    .header_div{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column-reverse;
                flex-direction: column-reverse;
    }
    #header{
        margin-top: 10px;
    }
    #header h1{
        font-size: 19px;
        text-align: center;
    }
    #header h2{
        font-size: 13px;
        text-align: center;
    }
    #header .get_started_btn{
        font-size: 13px;
        padding: 5px 17px;
    }
    #header .header-img{
        text-align: center;
    }
    #header .header-img img{
        height: 250px;
        width: 250px;
    }
    .info_div{
        padding: 30px 10px 10px 10px !important;
    }
    .info_div h2{
        margin: 2px 0px 2px 0px !important;
        padding: 0px 15px 0px 15px ;
    }
    .btn_div{
        margin: auto;
    }
}
/* contact page styling starts here */
.contact_div{
    height: inherit;
}
.profile_img{
    width: 200px;
    border-radius: 50%;
}
.contact_btns{
    font-weight: bold;
    font-size: 16px;
    padding: 10px 30px;
    border-radius: 7px;
    transition: .5s;
    text-decoration: none;
    height: 44px;
    width: 250px;
    text-align: center;
    color: white;
    margin-bottom: 20px;
}
.contact_btns img{
    height: 25px;
    width: 25px;
}
.contact_btns:nth-child(1){
    background-image: linear-gradient(26deg,#FEDA77 0%,#DD2A7B 25%,#8134AF 50%,#515BD4 75%);
    box-shadow: 0px 0px 20px rgba(119, 61, 183 , 0.5);
}
.contact_btns:nth-child(2){
    background-color: black;
    box-shadow: 0px 0px 20px rgba(0, 0, 0 , 0.5);
}
.contact_btns:nth-child(3){
    background-color: #eceff1;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, .25);
}
.contact_btns:nth-child(3) span{
    color: #f44336;
}
.contact_btns:nth-child(4){
    background-color: #64b161;
    box-shadow: 0px 0px 20px rgba(100, 181, 136, .6);
}
.contact_btns:nth-child(1):hover,.contact_btns:nth-child(2):hover,.contact_btns:nth-child(3):hover,.contact_btns:nth-child(4):hover{
    box-shadow: none;
}
.img_div{
    border-right: 1px solid #d9d6d6;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.btns_div{
    -webkit-align-items: flex-end;
            align-items: flex-end;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

/* contact page styling ends here */

/* responsive styling */
@media(max-width:425px){
    .contact_div .row{
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .contact_div h1{
        margin-bottom: 10px !important;
    }
    .img_div{
        width: 100%;
        border-right: none;
        -webkit-justify-content: center;
                justify-content: center;
    }
    .btns_div{
        width: 100%;
        -webkit-justify-content: center;
                justify-content: center;
        margin-top: 20px;
    }
    .contact_btns{
        margin-left: auto;
        margin-right: auto;
    }
}
/* service page css starts here */
.skills_card{
    text-align: center;
}
.card_img{    
    /* height: 320px; */
    width: 200px;
    /* border-radius: 50%; */
}
.card_img_div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    height: 200px;
}
.skills_card .card_info{
    margin-top: 20px;
    width: 50%;
    margin-right: auto;
    margin-left: auto;
}.skills_card .card_info p{
    font-size: 1rem;
}
/* service page css ends here */
/* navbar styling starts here */
.row>*{
    padding: 0px;
}
.container, .container-fluid, .container-lg, .container-md, .container-sm, .container-xl, .container-xxl{
    padding: 0px !important;
}
.navbar-toggler:focus{
    box-shadow: none;
}
.navbar{
    background-color: #fff !important;
}
.navbar-brand{
    font-size: 1.9rem !important;
    color: #3498db !important;
    margin-right: 0px;
}
.navbar a{
    font-size: 1.3rem;
}
.active{
    font-weight: bold;
    border-bottom: 1px solid #3498db;
}
.navbar a:hover{
    color: #3498db !important;
}
/* navbar styling ends here */

/* responsive styling */
@media(max-width:425px){
    .navbar-brand{
        font-size: 1.66rem !important;
    }
}
/* responsive code */
@media(max-width:425px){
    footer p{
        font-size: .8rem;
    }
}
